<template>
    <el-container>
        <el-header style="font-size: 40px; background-color: rgb(238, 241, 246)"
          >Quiz后台管理
            <el-button type="danger" size="mini" @click="logout" style="float: right; margin-top: 10px;">退出</el-button>
        </el-header>        
    <el-container>
            <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
                <el-menu :default-openeds="['1']">
                    <el-submenu index="1">
                        <template slot="title">管理选项</template>
                        <el-menu-item-group>
                            <el-menu-item index="1-1">
                                <router-link to="/user">用户管理</router-link>
                            </el-menu-item>
                            <el-menu-item index="1-2">
                                <router-link to="/question">题目管理</router-link>
                            </el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                </el-menu>
            </el-aside>

            <el-main>
                <!-- 顶部的查询表单 -->
                <el-form :inline="true" :model="formInline" class="demo-form-inline">
                    <el-form-item label="题目">
                        <el-input
                        v-model="formInline.keyword"
                        placeholder="请输入题目关键词"
                        ></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSearch">查询</el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="success" @click="onAddNewQuestion">添加题目</el-button>
                    </el-form-item>
                </el-form>
                
                <!-- 显示的table -->
                <el-table :data="tableData" style="width: 80%">
                    <el-table-column label="序号" width="50">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.id }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="题目" width="250">
                        <template slot-scope="scope">
                            <div slot="reference" class="name-wrapper">
                                <span style="margin-left: 10px">{{ scope.row.questionText }}</span>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column label="选项" width="260">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.options }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="答案" width="180">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.answer }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                            <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"
                            >删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <br>
                
                <!-- 分页 -->
                <el-pagination
                background
                layout="prev, pager, next"
                :page-size="pageSize"
                :total="total"
                :current-page="currentPage"
                @current-change="handlePageChange"
                >
                </el-pagination>

                <!-- 添加题目对话框 -->
                <el-dialog title="添加新题目" :visible.sync="dialogFormVisible">
                    <el-form :model="form">
                        <el-form-item label="题目" :label-width="formLabelWidth">
                            <el-input v-model="form.question" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="选项a" :label-width="formLabelWidth">
                            <el-input v-model="form.optiona" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="选项b" :label-width="formLabelWidth">
                            <el-input v-model="form.optionb" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="选项c" :label-width="formLabelWidth">
                            <el-input v-model="form.optionc" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="选项d" :label-width="formLabelWidth">
                            <el-input v-model="form.optiond" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="答案" :label-width="formLabelWidth">
                            <el-input v-model="form.answer" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="dialogFormVisible = false">取 消</el-button>
                        <el-button type="primary" @click="submitQuestion">确 定</el-button>
                    </div>
                </el-dialog>

                <!-- 编辑题目对话框 -->
                <el-dialog title="编辑题目" :visible.sync="editDialogVisible">
                    <el-form :model="editForm">
                        <el-form-item label="题目" :label-width="formLabelWidth">
                            <el-input v-model="editForm.question" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="选项a" :label-width="formLabelWidth">
                            <el-input v-model="editForm.optiona" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="选项b" :label-width="formLabelWidth">
                            <el-input v-model="editForm.optionb" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="选项c" :label-width="formLabelWidth">
                            <el-input v-model="editForm.optionc" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="选项d" :label-width="formLabelWidth">
                            <el-input v-model="editForm.optiond" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="答案" :label-width="formLabelWidth">
                            <el-input v-model="editForm.answer" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="editDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="submitEdit">确 定</el-button>
                    </div>
                </el-dialog>

                <!-- 成功提示对话框 -->
                <el-dialog
                    title="操作结果"
                    :visible.sync="successDialogVisible"
                    width="30%"
                    center>
                    <span>{{ successMessage }}</span>
                    <span slot="footer" class="dialog-footer">
                        <el-button type="primary" @click="successDialogVisible = false">确 定</el-button>
                    </span>
                </el-dialog>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            currentPage: 1,
            dialogFormVisible: false,
            pageSize: 5,
            total: 0,
            formInline: {
                keyword: "",
            },
            tableData: [],
            form: {
                question: "",
                optiona: "",
                optionb: "",
                optionc: "",
                optiond: "",
                answer: "",
            },
            editDialogVisible: false,
            editForm: {
                id: "",
                question: "",
                optiona: "",
                optionb: "",
                optionc: "",
                optiond: "",
                answer: "",
            },
            formLabelWidth: '120px',
            successDialogVisible: false,
            successMessage: "",
        }
    },
    methods: {
        logout() {
            // 删除 token
            localStorage.removeItem('jwt_token');
            // 跳转到登录页面
            this.$router.push('/login');
            this.$message({
                type: 'success',
                message: '已退出登录',
            });
        },
        onAddNewQuestion() {
            this.dialogFormVisible = true;
        },
        
        submitQuestion() {
            console.log("Submitting question:", this.form);
            axios
                .post("/addQuestion", this.form, {
                    headers: {
                        "Content-Type": "application/json",
                    },
                })
                .then((response) => {
                    console.log("Question added successfully:", response.data);
        
                    if (response.data.code === 1) {
                        this.successMessage = response.data.msg || "插入新问题成功";
                    } else {
                        this.successMessage = response.data.msg || "操作完成";
                    }
                    
                    this.successDialogVisible = true;
                    this.dialogFormVisible = false;

                    this.form = {
                        question: "",
                        optiona: "",
                        optionb: "",
                        optionc: "",
                        optiond: "",
                        answer: "",
                    };
                    this.handlePageChange(this.currentPage);
                })
                .catch((error) => {
                    console.error("Error adding question:", error);
                    this.successMessage = "添加题目失败，请重试";
                    this.successDialogVisible = true;
                });
        },
        
        onSearch() {
            console.log("Searching for:", this.formInline.keyword);
            axios
                .get(`/findQuestion?keyword=${this.formInline.keyword}`)
                .then((response) => {
                    this.tableData = response.data.data;
                    console.log("Search results:", response.data);
                })
                .catch((error) => {
                    console.error("Error searching questions:", error);
                });
        },
        
        handleEdit(index, row) {
            console.log("Editing question:", row);
            // 将当前行的数据填充到编辑表单中
            this.editForm = {
                id: row.id,
                question: row.questionText,
                optiona: row.options[0] || "",  // 从options数组中获取第一个选项
                optionb: row.options[1] || "",  // 从options数组中获取第二个选项
                optionc: row.options[2] || "",  // 从options数组中获取第三个选项
                optiond: row.options[3] || "",  // 从options数组中获取第四个选项
                answer: row.answer  // 直接使用answer字段
            };
            this.editDialogVisible = true;
        },
        
        // 根据题目数据获取正确答案的字母
        getCorrectAnswer(row) {
            if (row.answer1Correct) return "a";
            if (row.answer2Correct) return "b";
            if (row.answer3Correct) return "c";
            if (row.answer4Correct) return "d";
            return "";
        },
        
        submitEdit() {
            console.log("Updating question:", this.editForm);
            axios
                .post("/updateQuestion", this.editForm, {
                    headers: {
                        "Content-Type": "application/json",
                    },
                })
                .then((response) => {
                    console.log("Question updated successfully:", response.data);
            
                    if (response.data.code === 1) {
                        this.successMessage = response.data.msg || "更新问题成功";
                    } else {
                        this.successMessage = response.data.msg || "操作完成";
                    }
                    
                    this.successDialogVisible = true;
                    this.editDialogVisible = false;

                    // 刷新表格数据
                    this.handlePageChange(this.currentPage);
                })
                .catch((error) => {
                    console.error("Error updating question:", error);
                    this.successMessage = "更新题目失败，请重试";
                    this.successDialogVisible = true;
                });
        },
        
        handleDelete(index, row) {
            const id = row.id;
            this.$confirm("此操作将永久删除该题目, 是否继续?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
            })
            .then(() => {
                axios
                .get(`/delQuestion?id=${id}`)
                .then((response) => {
                    console.log(response.data);
                    this.handlePageChange(this.currentPage);
                })
                .catch((error) => {
                    console.error("Error deleting question:", error);
                    this.$message({
                    type: "error",
                    message: "删除失败，请稍后重试!",
                    });
                });
            })
            .catch(() => {
                this.$message({
                type: "info",
                message: "已取消删除",
                });
            });
        },
        
        handlePageChange(page) {
            this.currentPage = page;
            axios
                .get(`/questions?page=${this.currentPage}&pageSize=${this.pageSize}`)
                .then((response) => {
                this.tableData = response.data.data.qsBeanList;
                this.total = response.data.data.total;
                })
                .catch((error) => {
                console.error("Error fetching questions:", error);
                });
        },
    },
    mounted(){
        axios.get("/questions").then((response) => {
            console.log(response.data);
            const data = response.data.data.qsBeanList;
            this.tableData = data;
            this.total = response.data.data.total;
        }).catch((error) => {
            console.error("Error fetching questions:", error);
        });
        this.handlePageChange(1);
    },
}
</script>

<style></style>