<template>
    <el-container>
        <el-header style="font-size: 40px; background-color: rgb(238, 241, 246)"
          >Quiz后台管理
            <el-button type="danger" size="mini" @click="logout" style="float: right; margin-top: 10px;">退出</el-button>
        </el-header>       
    <el-container>
            <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
                <el-menu :default-openeds="['1']">
                    <el-submenu index="1">
                        <template slot="title">管理选项</template>
                        <el-menu-item-group>
                            <el-menu-item index="1-1">
                                <router-link to="/user">用户管理</router-link>
                            </el-menu-item>
                            <el-menu-item index="1-2">
                                <router-link to="/question">题目管理</router-link>
                            </el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                </el-menu>
            </el-aside>

            <el-main>
                <!-- 顶部的查询表单 -->
                <el-form :inline="true" :model="formInline" class="demo-form-inline">
                    <el-form-item label="用户名">
                        <el-input
                        v-model="formInline.keyword"
                        placeholder="请输入用户名关键词"
                        ></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSearch">查询</el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="success" @click="onAddNewUser">添加用户</el-button>
                    </el-form-item>
                </el-form>
                
                <!-- 显示的table -->
                <el-table :data="tableData" style="width: 80%">
                    <el-table-column label="ID" width="80">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.id }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="用户名" width="180">
                        <template slot-scope="scope">
                            <div slot="reference" class="name-wrapper">
                                <span style="margin-left: 10px">{{ scope.row.userName }}</span>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column label="密码" width="180">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.userPassword }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="用户角色" width="120">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">
                                {{ scope.row.userRole === 1 ? '管理员' : '普通用户' }}
                            </span>
                        </template>
                    </el-table-column>
                    <el-table-column label="更新时间" width="180">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ formatDate(scope.row.updateTime) }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                            <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"
                            >删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <br>
                
                <!-- 分页 -->
                <el-pagination
                background
                layout="prev, pager, next"
                :page-size="pageSize"
                :total="total"
                :current-page="currentPage"
                @current-change="handlePageChange"
                >
                </el-pagination>

                <!-- 添加用户对话框 -->
                <el-dialog title="添加新用户" :visible.sync="dialogFormVisible">
                    <el-form :model="form">
                        <el-form-item label="用户名" :label-width="formLabelWidth">
                            <el-input v-model="form.username" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="密码" :label-width="formLabelWidth">
                            <el-input v-model="form.password" autocomplete="off" show-password></el-input>
                        </el-form-item>
                        <el-form-item label="确认密码" :label-width="formLabelWidth">
                            <el-input v-model="form.checkpassword" autocomplete="off" show-password></el-input>
                        </el-form-item>
                        <el-form-item label="用户角色" :label-width="formLabelWidth">
                            <el-select v-model="form.userRole" placeholder="请选择用户角色">
                                <el-option label="普通用户" :value="0"></el-option>
                                <el-option label="管理员" :value="1"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="dialogFormVisible = false">取 消</el-button>
                        <el-button type="primary" @click="submitUser">确 定</el-button>
                    </div>
                </el-dialog>

                <!-- 编辑用户对话框 -->
                <el-dialog title="编辑用户" :visible.sync="editDialogVisible">
                    <el-form :model="editForm">
                        <el-form-item label="用户名" :label-width="formLabelWidth">
                            <el-input v-model="editForm.userName" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="密码" :label-width="formLabelWidth">
                            <el-input v-model="editForm.userPassword" autocomplete="off" show-password placeholder="留空表示不修改密码"></el-input>
                        </el-form-item>
                        <el-form-item label="用户角色" :label-width="formLabelWidth">
                            <el-select v-model="editForm.userRole" placeholder="请选择用户角色">
                                <el-option label="普通用户" :value="0"></el-option>
                                <el-option label="管理员" :value="1"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="editDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="submitEdit">确 定</el-button>
                    </div>
                </el-dialog>

                <!-- 成功提示对话框 -->
                <el-dialog
                    title="操作结果"
                    :visible.sync="successDialogVisible"
                    width="30%"
                    center>
                    <span>{{ successMessage }}</span>
                    <span slot="footer" class="dialog-footer">
                        <el-button type="primary" @click="successDialogVisible = false">确 定</el-button>
                    </span>
                </el-dialog>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            currentPage: 1,
            dialogFormVisible: false,
            editDialogVisible: false,
            pageSize: 5,
            total: 0,
            formInline: {
                keyword: "",
            },
            tableData: [],
            form: {
                username: "",
                password: "",
                checkpassword: "",
                userRole: 0
            },
            editForm: {
                id: "",
                userName: "",
                userPassword: "",
                userRole: 0
            },
            formLabelWidth: '120px',
            successDialogVisible: false,
            successMessage: "",
        }
    },
    methods: {
        logout() {
            localStorage.removeItem('jwt_token');
            localStorage.removeItem('role');
            this.$router.push('/login');
            this.$message({ type: 'success', message: '已退出登录' });
        },
        onAddNewUser() {
            this.dialogFormVisible = true;
        },
        
        submitUser() {
            console.log("Submitting user:", this.form);
            // 后端 /addUser 需要字段 userrole（字符串），不是 userRole
            const submitData = {
                username: this.form.username,
                password: this.form.password,
                checkpassword: this.form.checkpassword,
                userrole: String(this.form.userRole) // 0 或 1
            };
            axios
                .post("/addUser", submitData, {
                    headers: { "Content-Type": "application/json" },
                })
                .then((response) => {
                    console.log("User added successfully:", response.data);
                    if (response.data.code === 1) {
                        this.successMessage = response.data.msg || "添加用户成功";
                    } else {
                        this.successMessage = response.data.msg || "操作完成";
                    }
                    this.successDialogVisible = true;
                    this.dialogFormVisible = false;
                    this.form = { username: "", password: "", checkpassword: "", userRole: 0 };
                    this.handlePageChange(this.currentPage);
                })
                .catch((error) => {
                    console.error("Error adding user:", error);
                    this.successMessage = "添加用户失败，请重试";
                    this.successDialogVisible = true;
                });
        },
        
        onSearch() {
            console.log("Searching for:", this.formInline.keyword);
            axios
                .get(`/findUser?keyword=${this.formInline.keyword}`)
                .then((response) => {
                    this.tableData = response.data.data;
                    console.log("Search results:", response.data);
                })
                .catch((error) => {
                    console.error("Error searching users:", error);
                });
        },
        
        handleEdit(index, row) {
            console.log("Editing user:", row);
            this.editForm = {
                id: row.id,
                userName: row.userName,
                userPassword: "",
                userRole: row.userRole
            };
            this.editDialogVisible = true;
        },
        
        submitEdit() {
            console.log("Updating user:", this.editForm);
            const updateData = {
                id: this.editForm.id,
                userName: this.editForm.userName,
                userPassword: this.editForm.userPassword,
                userRole: this.editForm.userRole
            };
            axios
                .post("/updateUser", updateData, {
                    headers: { "Content-Type": "application/json" },
                })
                .then((response) => {
                    console.log("User updated successfully:", response.data);
                    if (response.data.code === 1) {
                        this.successMessage = response.data.msg || "用户更新成功";
                    } else {
                        this.successMessage = response.data.msg || "操作完成";
                    }
                    this.successDialogVisible = true;
                    this.editDialogVisible = false;
                    this.handlePageChange(this.currentPage);
                })
                .catch((error) => {
                    console.error("Error updating user:", error);
                    this.successMessage = "更新用户失败，请重试";
                    this.successDialogVisible = true;
                });
        },
        
        handleDelete(index, row) {
            const id = row.id;
            this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
              confirmButtonText: "确定",
              cancelButtonText: "取消",
              type: "warning",
            })
            .then(() => {
                axios
                .get(`/deleteById?id=${id}`)
                .then((response) => {
                    console.log(response.data);
                    this.handlePageChange(this.currentPage);
                })
                .catch((error) => {
                    console.error("Error deleting user:", error);
                    this.$message({ type: "error", message: "删除失败，请稍后重试!" });
                });
            })
            .catch(() => {
                this.$message({ type: "info", message: "已取消删除" });
            });
        },
        
        handlePageChange(page) {
            this.currentPage = page;
            axios
                .get(`/users?page=${this.currentPage}&pageSize=${this.pageSize}`)
                .then((response) => {
                    this.tableData = response.data.data.row;
                    this.total = response.data.data.total;
                })
                .catch((error) => {
                    console.error("Error fetching users:", error);
                });
        },
        
        formatDate(dateString) {
            if (!dateString) return '';
            const date = new Date(dateString);
            return date.toLocaleString();
        }
    },
    mounted(){
        axios.get("/users").then((response) => {
            console.log(response.data);
            const data = response.data.data.row;
            this.tableData = data;
            this.total = response.data.data.total;
        }).catch((error) => {
            console.error("Error fetching users:", error);
        });
        this.handlePageChange(1);
    },
}
</script>

<style></style>