<template>
  <el-container>
    <el-header style="font-size: 40px; background-color: rgb(238, 241, 246);display: flex; justify-content: center; align-items: center;">
      Quiz后台管理系统登录
    </el-header>
    <el-main>
      <el-form :model="form" :rules="rules" ref="loginForm" label-width="80px" class="login-form">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="form.username" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="form.password" type="password" placeholder="请输入密码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">登录</el-button>
        </el-form-item>
      </el-form>
    </el-main>
  </el-container>
</template>

<script>
import axios from "axios";

export default {
  name: 'LoginView',
  data() {
    return {
      form: {
        username: '',
        password: '',
      },
      rules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
      },
      // 后端接口路径（使用 main.js 的 axios.defaults.baseURL）
      LOGIN_PATH: '/login',
    };
  },
  methods: {
    onSubmit() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.login();
        } else {
          this.$message.error('表单验证失败');
        }
      });
    },

    async login() {
      try {
        const res = await axios.post(this.LOGIN_PATH, {
          username: this.form.username,
          password: this.form.password
        }, {
          headers: { 'Content-Type': 'application/json' }
        });

        const { code, data, msg } = res.data || {};
        if (code !== 1) {
          this.$message.error('登录失败：' + (msg || '未知错误'));
          return;
        }

        // 后端建议返回 { token, role }，兼容旧结构仅返回 token 的情况
        const token = (data && (data.token ?? data)) || '';
        const role = (data && data.role);
        if (!token) {
          this.$message.error('登录失败：未获取到令牌');
          return;
        }

        // 保存 token
        localStorage.setItem('jwt_token', token);

        // 必须为管理员角色才能进入后台
        if (Number(role) !== 1) {
          this.$message.error('无管理员权限');
          // 可选：清理 token
          // localStorage.removeItem('jwt_token');
          return;
        }
        localStorage.setItem('role', String(role));

        this.$message.success('登录成功');
        // 跳转到管理员首页（用户管理）
        this.$router.push('/user');
      } catch (err) {
        console.error('登录异常:', err);
        this.$message.error('登录异常');
      }
    }
  },
};
</script>

<style>
.login-form {
  max-width: 400px;
  margin: 50px auto;
}
</style>