import Vue from 'vue'
import VueRouter from 'vue-router'
import UserView from '../views/elment/UserView.vue'
import QuestionView from '@/views/elment/QuestionView.vue'
import LoginView from '@/views/elment/LoginView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path:'/user',
    name: 'user',
    component: UserView,
    meta: {
      requiresAuth: true,
      requiresAdmin: true // 需要管理员权限
    }
  },
  {
    path:'/question',
    name:'question',
    component:QuestionView,
    meta: {
      requiresAuth: true,
      requiresAdmin: true // 需要管理员权限
    }
  },
  {
    path:'/login',
    name:'login',
    component: LoginView
  }
]

const router = new VueRouter({
  routes
})

// 全局导航守卫：同时校验登录状态与管理员角色
router.beforeEach((to, from, next) => {
  const needAuth = to.matched.some(record => record.meta.requiresAuth);
  const needAdmin = to.matched.some(record => record.meta.requiresAdmin);

  if (!needAuth) {
    next();
    return;
  }

  const token = localStorage.getItem('jwt_token');
  if (!token) {
    next('/login');
    return;
  }

  if (needAdmin) {
    const role = Number(localStorage.getItem('role'));
    if (role !== 1) {
      // 无管理员权限，回登录页或提示
      next('/login');
      return;
    }
  }

  next();
});

export default router